* {
    box-sizing: border-box;
}

.App {
    display: flex;
    align-items: center;
    justify-content: center;

    height: 100vh;

    background: #1D1D2C;
}

@media only screen and (max-width: 850px) {
    .App {
        flex-direction: column;
    }
}

.container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;

    width: 355px;
    height: 255px;

    margin: 30px;

    border: solid 2px #3CBCC3;
    border-radius: 12px;
}

.counter {
    font-size: large;
    color: #3CBCC3;

    width: 290px;
    height: 100px;
    margin: 30px 0 16px;
    padding: 0 0 7px;
    composes: container;
}

.buttons {
    justify-content: space-around;
    flex-direction: row;
    height: 55px;
    margin: 15px 0 30px;
    composes: counter;
    padding: 0;
}

.button {
    composes: counter;
    width: 80px;
    height: 40px;
    color: #1D1D2C;
    background: #3CBCC3;
    padding: 0;
    margin: 0;
}

.button:disabled {
    color: #1D1D2C;
    background-color: #3CBCC3;
    opacity: .2;
}

.button:focus {
    outline: none;
}

.button:active {
    color: #3CBCC3;
    border-color: #1D1D2C;
    background: #1D1D2C;
}

.upCountMax {
    font-size: 52px;

    color: #FE4A49;
}

.countNumber {
    font-size: 32px;
}

.countIncorrect {
    color: #FE4A49;
}

.value {
    font-size: large;
    width: 66px;
    margin-left: 39px;

    border: none;
    border-radius: 12px;
    color: #3CBCC3;
    background: #1D1D2C;
}

.value:focus {
    outline: none;
}

.valueError {
    color: #E40C2B;
    composes: value;
}
